.support {
  padding-top: 40px;
  display: none;
}

.link-book-shop {
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.shopping-list-title {
  color: var(--text-color);
  letter-spacing: -1.28px;
  margin-bottom: 120px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.18;
}

.s-l-title {
  color: var(--violet);
}

.text-shop-list {
  color: #0009;
  text-align: center;
  letter-spacing: -.28px;
  margin-bottom: 14px;
  line-height: 1.28;
}

.shop-section {
  padding-top: 40px;
}

.message {
  width: 265px;
  height: 248px;
  margin: 0 auto;
}

.shopping-card {
  background: #fff;
  border: 2px solid #4f2ee866;
  border-radius: 16px;
  height: 170px;
  padding: 14px;
  display: flex;
  position: relative;
}

.shopping-image {
  border-radius: 8px;
  height: 142px;
  margin-right: 14px;
}

.books-sh-container {
  width: 193px;
}

.title-shop-list {
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 144px;
  overflow: hidden;
}

.shopping-category {
  color: var(--top-category-title);
  letter-spacing: .36px;
  margin-bottom: 8px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.16;
}

.shopping-description {
  color: var(--text-color);
  letter-spacing: -.56px;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28;
  display: -webkit-box;
  overflow: hidden;
}

.shopping-description-container {
  height: 68px;
  margin-bottom: 16px;
}

.shopping-author {
  color: var(--top-category-title);
  letter-spacing: -.48px;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 96px;
  font-size: 12px;
  font-style: italic;
  line-height: 1.16;
  overflow: hidden;
}

.delete-button {
  background-color: #4f2ee8;
  border: none;
  border-radius: 50px;
  width: 28px;
  height: 28px;
  position: absolute;
  top: 14px;
  right: 14px;
}

.shopping-svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.amazon-img {
  max-width: 32px;
  height: 11px;
}

.shopping-img-store {
  width: 16px;
  height: 16px;
}

.list-shops {
  align-items: center;
  gap: 8px;
  display: flex;
}

.book-list {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.shopping-descr {
  width: 193px;
}

@media screen and (min-width: 768px) {
  .shopping-list-title {
    letter-spacing: -1.92px;
    margin-bottom: 140px;
    font-size: 48px;
    line-height: 1.08;
  }

  .text-shop-list {
    letter-spacing: -.36px;
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 1.33;
  }

  .message {
    width: 349px;
    height: 309px;
  }

  .shop-img {
    width: 322px;
    height: 241px;
    margin: 0 auto;
  }

  .shopping-image {
    width: 116px;
    height: 165px;
    margin-right: 24px;
  }

  .book-card-title {
    width: 166px;
  }

  .shopping-description-container {
    height: 53px;
    margin-bottom: 32px;
  }

  .shopping-card {
    height: 213px;
    padding: 24px;
  }

  .amazon-img {
    width: 48px;
    height: 15px;
  }

  .shopping-img-store {
    width: 28px;
    height: 27px;
  }

  .list-shops {
    gap: 16px;
  }

  .shopping-author, .book-card-title {
    white-space: normal;
    text-overflow: initial;
    width: auto;
    overflow: visible;
  }

  .shopping-descr {
    width: 508px;
  }
}

@media screen and (min-width: 1440px) {
  .support {
    display: block;
  }

  .shopping-list-title {
    margin-bottom: 140px;
  }

  .message {
    padding: 10px;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translateY(-50%);
  }

  .shopping-description {
    white-space: normal;
    text-overflow: initial;
    width: auto;
    overflow: visible;
  }

  .shopping-descr {
    width: 808px;
  }
}

